import React, {ChangeEvent, FC, useCallback, useState} from 'react'
import {useDispatch, useSelector} from "react-redux";

import './login.scss'
import {StoreState} from "../../store";
import {AuthState} from "../../store/auth/types";
import {Input} from "../../components/Input/Input";
import {Button} from "../../components/Button/Button";
import {loginUserAction} from "../../store/auth/actions";

enum LoginFieldName {
  username = 'username',
  password = 'password'
}

type FormError = {
  username: string | null
  password: string | null
}

const Login: FC = () => {
  const { loading, error } = useSelector<StoreState, AuthState>(s => s.auth)
  const dispatch = useDispatch()
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')


  const [formError, setFormError] = useState<FormError>({
    username: null,
    password: null
  })

  const setFieldValue = useCallback((fieldName: LoginFieldName) => ({ target: { value }}: ChangeEvent<HTMLInputElement>) => {
    if (fieldName === LoginFieldName.password) {
      setPassword(value)
    } else {
      setUsername(value)
    }
  }, [setPassword, setUsername])


  const submit = useCallback(() => {
    const err: FormError = {
      username: null,
      password: null
    }
    if (!username.trim()) {
      err.username = 'Username is Required'
    }
    if (!password.trim()) {
      err.password = 'Password is Required'
    }

    setFormError(err)
    if (err.password || err.username) {
      return
    }

   dispatch(loginUserAction(username, password))
  }, [username, password, setFormError, dispatch])

  return (
    <div className={'login'}>
      <div className="login__container">
        <h1>Login to Your Account</h1>
        {error && <span className={'login__container--error'}>{error}</span> }
        {loading && <div className={'login__container--loader'}>Please Wait...</div> }
        <div className={'login__container--form'}>
            <Input error={formError.username} label={'username'} id={'username'} value={username} onChange={setFieldValue(LoginFieldName.username)}/>
            <Input error={formError.password} label={'password'} id={'password'} value={password} onChange={setFieldValue(LoginFieldName.password)} />
            <Button onClick={submit} disabled={loading}>Login</Button>
        </div>
      </div>
    </div>
  )
}

export default Login
